<template>
  <div class="parent">
    <div class="top">
      <div v-show="name" style="float: left;margin-left: 10px">姓名：{{name}}</div>
      <div v-show="mj" style="float: left;margin-left: 10px">地块面积(亩)：{{mj}}</div>
      <div @click="returnClick" style="float: right;margin-right: 10px">施肥建议></div>
    </div>
    <div id="map"></div>
    <div class="card">
      <el-card class="box-card info" v-show="showInfo">
        <div slot="header" class="clearfix">
          <span>种植结构信息</span>
          <el-button style="float: right; padding: 3px 0" icon="el-icon-close" type="text" @click="click"></el-button>
        </div>
        <canvas id="canvas" v-show="false"></canvas>
        <el-row :gutter="22">
          <el-col :span="4">id</el-col>
          <el-col :span="18">
            <div class="grid-content bg-purple">{{id}}</div>
          </el-col>
        </el-row>
        <el-row :gutter="22">
          <el-col :span="4">qh</el-col>
          <el-col :span="18">
            <div class="grid-content bg-purple">{{qhmc}}</div>
          </el-col>
        </el-row>
        <el-row :gutter="22">
          <el-col :span="4">zw</el-col>
          <el-col :span="18">
            <div class="grid-content bg-purple">{{zwmc}}</div>
          </el-col>
        </el-row>
      </el-card>
    </div>
  </div>
</template>

<script>
  import mapboxgl from 'mapbox-gl';
  import coordinateText from './CoordinateText';

  export default {
    components: {coordinateText},
    name: 'TestMap',
    data() {
      return {
        map: null,
        hasLoaded: false,
        showInfo: false,
        id: null,
        qhmc: null,
        zwmc: null,
        name: '',
        mj: 0
      };
    },
    mounted() {
      var canvas = document.getElementById('canvas');
      var gl = canvas.getContext('webgl');
      if (!gl) {
        console.log('当前浏览器版本不支持webgl，请升级或切换浏览器');
        alert('不支持webgl');
        window.android.report();
      }
      if (!mapboxgl.supported()) {
        window.android.report();
        alert('Your browser does not support Mapbox GL');
      }
      mapboxgl.accessToken = 'pk.eyJ1IjoiaGFtYnVnZXJkZXZlbG9wIiwiYSI6ImNqNXJtZjF4ZzB3em4yd21pZmVqbHlleDAifQ.I9eqVjtotz7jaU7XcJm9pQ';
      this.map = new mapboxgl.Map({
        container: 'map',
        style: 'http://gykj123.cn:9021/api/v1/styles/gykj/S1wsBE2lH',
        center: [114.08316983, 33.615693],
        zoom: 14
      });
      const that = this;
      this.map.on('style.load', function() {
        //添加高亮图层
        if (!that.hasLoaded) {
          that.hasLoaded = true;
          that.map.addLayer({
            'id': '种植结构高亮',
            'source': 'dt_zzjg',
            'type': 'line',
            'minzoom': 1,
            'maxzoom': 23,
            'layout': {
              'visibility': 'visible'
            },
            'paint': {
              'line-color': '#00ffff',
              'line-width': 5
            },
            'source-layer': 'dt_zzjg',
            'metadata': {
              'mapbox:group': '04687df45bbae'
            },
            'visibility': 'none',
            'filter': [
              'in',
              'objectid',
              ''
            ]
          }); // Place polygon under these labels.
        }
        that.addControl();

        that.map.on('mousemove', function(e) {
          that.lng = e.lngLat.lng;
          that.lat = e.lngLat.lat;
        });
        //点击查询
        that.map.on('click', '种植结构', function(e) {
          let bbox = [[e.point.x, e.point.y], [e.point.x, e.point.y]];
          let features = that.map.queryRenderedFeatures(bbox, {layers: ['种植结构']});
          if (features.length > 0) {
            let filter = features.reduce(function(memo, feature) {
              memo.push(feature.properties.objectid);
              return memo;
            }, ['in', 'objectid']);
            let coordinates = features[0].geometry.coordinates[0];
            let lngMin = coordinates[0][0];
            let lngMax = coordinates[0][0];
            let latMin = coordinates[0][1];
            let latMax = coordinates[0][1];
            for (let i = 1; i < coordinates.length; i++) {
              if (lngMin > coordinates[i][0]) {
                lngMin = coordinates[i][0];
              } else if (lngMax < coordinates[i][0]) {
                lngMax = coordinates[i][0];
              }
              if (latMin > coordinates[i][1]) {
                latMin = coordinates[i][1];
              } else if (lngMax < coordinates[i][1]) {
                latMax = coordinates[i][1];
              }
            }

            that.map.fitBounds(new mapboxgl.LngLatBounds([lngMin, latMin], [lngMax, latMax]), {
              padding: 60
            });


            that.showInfo = true;
            that.id = features[0].properties.objectid;
            that.qhmc = features[0].properties.qhmc;
            that.zwmc = features[0].properties.zwmc;
            that.map.setFilter('种植结构高亮', filter);
          } else {
            that.showInfo = false;
          }
        });

      });
      console.log(this.getQueryString('name'));
      console.log(this.getQueryString('mj'));
      this.name = this.getQueryString('name');
      this.mj = this.getQueryString('mj');
    },
    methods: {
      /**
       * 添加控件
       */
      addControl: function() {
        let scale = new mapboxgl.ScaleControl({
          maxWidth: 80,
          unit: 'imperial'
        });
        this.map.addControl(scale);
        // this.map.addControl(new mapboxgl.FullscreenControl({container: document.querySelector('body')}));
      },
      click: function() {
        this.showInfo = false;
        this.map.setFilter('种植结构高亮', ['in', 'objectid', '']);
      },
      returnClick: function() {
        wx.miniProgram.navigateBack({delta: 1});
        // wx.miniProgram.navigateTo({
        //   url: '../login/login'
        // })
      },
      getQueryString: function(variable) {
        let url= decodeURI(window.location.href);
        if(url.indexOf("?")==-1){
          return
        }
        let array=url.split("?");
        var query = array[1];
        var vars = query.split('&');
        for (var i = 0; i < vars.length; i++) {
          var pair = vars[i].split('=');
          if (pair[0] == variable) {
           return pair[1];
          }
        }
        return (false);
      }
    }
  };
</script>

<style>
  #map, .parent {
    width: 100%;
    height: 100%;
  }

  #map {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
  }

  .parent .card {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1000;
    /*width: 30%;*/
  }

  .top {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.1);
    color: white;
    padding-left: 5px;
  }
</style>



