<template>
  <el-tag type="danger">{{text}}</el-tag>
</template>

<script>
  export default {
    name: "CoordinateText",
    data() {
      return {

      }
    },
    props: ["lng","lat"],
    methods: {},
    computed:{
      text:function () {
        return "经度："+this.lng.toFixed(2)+" 纬度："+this.lat.toFixed(2)
      }
    }
  }
</script>

<style scoped>
  .el-tag {
    position: absolute;
    z-index: 200;
    bottom: 0;
    left: 50%;
    transform: translateY(-70%);
  }
</style>



